export default {
  data() {
    return {
		orderData:''
	};
  },
  onLoad(options){
	  this.orderData = options.orderData
  },
  methods: {
    linkTo(title) {
      uni.navigateTo({
        url: `returnForm?type=${title}&orderData=${this.orderData}`
      });
    }
  }
};
